@import url('https://fonts.googleapis.com/css2?family=Belleza&family=Raleway:wght@400;500;700&family=Roboto+Condensed:wght@400;500;700&display=swap');

.landing-content-container{
    width: 100vw;
    height: 80vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    text-align: center;
}

.landing-inner-container{
    color: #e6e5e5;
    width: 70%;
    height: 70%;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
}

.landing-inner-container h1{
    font-family: 'Belleza', sans-serif;
    font-weight: 200;
    font-size: 3rem;
}

.landing-inner-container p{
    font-family: 'Raleway', sans-serif;
    font-size: 1.3rem;
    font-weight: 100;
}

.landing-inner-container button{
    font-family: 'Roboto Condensed', sans-serif;
    width: 80%;
    align-self: center;
    font-size: 1.2rem;
    padding: 0.8rem;
    background-color: transparent;
    border: 1px solid #e6e5e5;
    color: #e6e5e5;
    margin: 1rem 0;
    transition: 0.8s ease;
}

.landing-inner-container button:hover{
    background-color: #e6e5e5;
    color: #222222;
}

@media screen and (min-width: 1024px) {

    .landing-content-container{
        height: 70vh;
        text-align: left;
    }

    .landing-inner-container h1{
        font-size: 4.5rem;
        margin-bottom: 0.5rem;
    }

    .landing-inner-container p{
        font-size: 1.5rem;
    }

    .landing-buttons{
        margin-top: 1rem;
        display: flex;
        justify-content: start;
    }

    .landing-buttons button{
        width: unset;
        padding: 1rem 5rem;
        margin-right: 3rem;
    }
}